.button-show-more__wrapper{
    button{
        background: #2196F3;
        border: 0 #2196F3;
        border-radius: 5px;
        width: 100%;
        padding: 15px 20px;

        margin-top: 20px;
        

        font-size: 12px;
        line-height: 20px;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        color: #FFFFFF;
    }
    :hover { 
        cursor: pointer;
        // transform: scale(1.1);
        // transition: transform 0.5s; 
        font-size: 14px;
        transition:  font-size 0.5s; 
    }
}

